import {SEARCH_MOVIE, FETCH_MOVIE, FETCH_MOVIE_DETAIL, LOADING} from '../actions/types.js'

const initialState = {
    text: '',
    movies: [],
    movie: [],
    loading: false
};

export default function(state = initialState, action) {
    switch(action.type) {
        case SEARCH_MOVIE:
            return {
                ...state,
                text: action.payload,
            };

        case FETCH_MOVIE:
            return {
                ...state,
                movies: action.payload,
                loading: false
            }

        case FETCH_MOVIE_DETAIL:
            return {
                ...state,
                movie: action.payload,
                loading: false
            }

        case LOADING:
            return {
                ...state,
                loading: true
            }
                                                                                                                                                                                             
        default:
            return state;
    }
}